import { _decorator,  Component,  Vec3 } from "cc";
const { ccclass, property } = _decorator;

@ccclass("GravityHandler")
export class GravityHandler extends Component {
  onLoad() {
    super.onLoad();
  }

  async applyGravity(grid) {
    let moved = false;
    let promises: Promise<void>[] = [];
    
    do {
      moved = false;

      for (let col = 0; col < grid[0].length; col++) {
        for (let row = grid.length - 2; row >= 0; row--) { 
          const currentPiece = grid[row][col];
          const belowPiece = grid[row + 1][col];

          // Eğer aşağıdaki hücre boşsa, yukarıdaki hücrenin node'unu hareket ettir
          if (belowPiece.isEmpty && !currentPiece.isEmpty) {
            // Aşağıdaki hücreye, yukarıdaki hücrenin node'unu taşı
            const belowNode = belowPiece.node;
            const currentNode = currentPiece.node;

            // Aşağıdaki hücreye yukarıdaki hücrenin node'unu atama
            belowPiece.node = currentNode;
            currentPiece.node = belowNode;

            // Yeni pozisyonu güncelle ve hareket ettir
            promises.push(belowPiece.node.setPosition(new Vec3(col, row + 1, 0)));
            moved = true;
          }
        }
      }
      
      await Promise.all(promises);
    } while (moved); 
  }
}
